a, u{
  text-decoration: none;
  color: white;
}


.download-background {
	background-image: -webkit-gradient(linear, left top, left bottom, from(#41414D), to(#3B3B47));
	background-image: -webkit-linear-gradient(top, #41414D, #3B3B47);
	background-image: -o-linear-gradient(top, #41414D, #3B3B47);
	background-image: linear-gradient(to bottom, #41414D, #3B3B47);
	-webkit-box-shadow: 0 1px 1px #33333D;
	        box-shadow: 0 1px 1px #33333D;
}


.download-background:active {
  background: none;
  background-image: -webkit-gradient(linear, left top, right top, from(#363642), to(#3B3B47));
  background-image: -webkit-linear-gradient(left, #363642, #3B3B47);
  background-image: -o-linear-gradient(left, #363642, #3B3B47);
  background-image: linear-gradient(to right, #363642, #3B3B47);
	-webkit-box-shadow: inset 1px 0 1px #33333D;
	        box-shadow: inset 1px 0 1px #33333D;
}



